//VUE 
var Vue = require('vue');
var VueRouter = require('vue-router');
Vue.use(VueRouter);
var VueAsyncData = require('vue-async-data');
Vue.use(VueAsyncData);
var App = Vue.extend(require('./app.vue'));

$.ajaxSettings.crossDomain = true;

//
var fastclick = require('fastclick');
fastclick.attach(document.body);

//  Mock
window.Mock = require('mockjs');

var router = new VueRouter({
    //abstract:true,
    //
    //
    //history: false,   // HTML5 history  404
    //saveScrollPosition: false
    hashbang: true,
    history: false,
    saveScrollPosition: true,
    transitionOnLoad: true,
    linkActiveClass:'nav-active' // http://vuejs.github.io/vue-router/en/link.html
});

//

window.routeList=[];

router.beforeEach(function (transition) {
    //
    //$("html, body, .page").removeClass("scroll-hide");

    //

    // if(transition.to.auth){
    // 	console.log('');
    // }

    if(transition.to.name == 'forbidden'){

        router.app.authenticating = true;
        setTimeout(function(){
            router.app.authenticating = false;
            alert('');
            transition.abort();
        },1500);
    }

    if(routeList.length > 1 && transition.to.name==routeList[routeList.length-2]['name']){

        router.app.effect='back';

        routeList.splice(routeList.length-1,1);

    } else {

        router.app.effect='fade';

        routeList.push({
            name : transition.to.name,
            path : transition.to.path,
            query : transition.to.query,
            params : transition.to.params,
            timer: +new Date
        });

    }

    transition.next();
});

//
router.afterEach(function (transition) {
    console.log(': ' + transition.to.path)
    //$.refreshScroller();
});

require('./router')(router);

router.start(App,'#app');


