module.exports = function(router){
    //
    router.map({
        //
        '/':{
            name:'index',
            component: require('./app.vue')

        },
        //
        '/list':{
            name:'list',
            component: require('./views/list.vue')
        },
        //
        '/detail/:goodsid':{
            name:'detail',
            component: require('./views/detail.vue')
        },

        //
        '/cart':{
            name:'cart',
            component: require('./views/cart.vue')
        },



        //
        '/user/:userid':{
            name:'user',
            component: require('./views/user.vue')
        },

    });

};


